.port_wrap {
	display: grid;
	grid-template-columns: 1fr 1fr;
}
.port_wrap_iten {
border: 1px solid red;
}
.block_portfolio {
	padding: 10px;
		display: grid;
	grid-template-columns: repeat(2, minmax(100px, auto));
//	grid-auto-rows:minmax(100px, auto);
	grid-auto-rows: minmax(200px, 1fr);
