width: 100%;
height: 100%;
background-color: rgba(#000, .8);
top:0;
left:0;
content: '';
		}
		.block_portfolio_items a {
color:#fff;
z-index: 1;
text-decoration: none;
		}
.block_portfolio_items a:hover {
