margin: 10px 0 10px 0;
	}










.masonry {
	padding: 2vmin;
	min-width: 320px;
	display: flex;
	flex-wrap: wrap;
}

.masonry > div {
background-size: cover;
	margin: 0.5rem;
	min-height: 30vmin;
	display: flex;
	overflow: hidden;
	align-items: center;
	justify-content: center;

}

.masonry div:nth-child(3n+1) {
	flex: 1 1 15%;
}

.masonry div:nth-child(3n+2) {
      flex: 2 1 20%;
}
.masonry div:nth-child(3n+3) {
  flex: 2 1 27%;
}

.masonry div:nth-child(3n+4) {
  flex: 2 1 calc(100% -27% - 20% - 15%);
}


.masonry > * img {
display: block;
width: 100%;
height: auto;
}
.masonry > div {
position: relative;
}
.masonry > div:before {
position: absolute;
width: 100%;
height: 100%;
top:0;
left:0;
background-color: #000;
opacity: .85;
content:"";
}
.masonry > div:hover:before {
opacity: .45;
}



.masonry > div a {
color:red;
font-size: 20px;
font-weight: bold;
z-index: 1;
}








